body {
  margin: 0;
  font-family: system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #73453ee1;
}

.welcome {
  margin: 40px auto;
  max-width: 580px;
  display: flex;
  justify-content: space-between;

  .logo {
    margin-top: -20px;
    width: 100px;
    height: 90px;
    cursor: pointer;
  }

  h1 {
    margin-top: -11px;
    text-transform: uppercase;
    font-family: verdana;
    font-size: 55px;
    color: #f5f5f5;
    text-shadow:
      1px 1px 1px #919191,
      1px 2px 1px #919191,
      1px 3px 1px #919191,
      1px 4px 1px #919191,
      1px 10px 5px rgba(16, 16, 16, 0.4);
  }
}

.header {
  padding-top: 2px;
  text-align: center;
  font-family: verdana;
  color: #73453e;
  font-weight: 700;
}

.wrapper {
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.40);
  border-radius: 10px;
  max-width: 1070px;
  margin: -40px auto;
  margin-bottom: 50px;
  padding-bottom: 20px;
}

.plan {
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.40);
  border-radius: 10px;
  max-width: 1070px;
  margin: -40px auto;
  height: 700px;

  h1 {
    padding-top: 20px;
    text-align: center;
  }
}

.items {
  padding-top: 10px;
  display: flex; 
  flex-wrap: wrap;

  h3 {
    color: #73453e;
    margin-left: 20px;
    padding-bottom: 200px;
  }
}

.bestellung {
  h1 {
    padding-top: 5px;
    text-align: center;
  }
}
