.essenkarte {
    border: 1px solid #73453ee1;
    padding: 10px;
    margin-top: 20px;
    margin-bottom: 5px;
    margin-left: 30px;
    width: 155px;
    height: 255px;
    border-radius: 7px;
    transition: box-shadow 0.2s ease-in-out, transform 0.2 ease-in-out;

    &:hover {
        box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.15);
        transform: translateY(-5px);
    }

    h5 {
        font-weight: 400;
        font-size: 14px;
    }

    p {
        font-size: 15px;
    }
}

.image {
    border-radius: 4px;
}

.buchen {
    display: flex;
    justify-content: space-between;

    p {
        margin-top: -5px;
    }
}

.bezeichnung {
    p {
        border: 1px solid #73453ee1;
        border-radius: 3px;
        font-size: small;
        text-align: center;
        margin-top: 0%;
    }

    h5 {
        border: 1px solid #73453ee1;
        border-radius: 3px;
        font-size: small;
        text-align: center;
        margin-top: 0%;
        margin-top: -10px;
    }
}

.add {
    margin-right: 10px;
    margin-top: -10px;
    width: 27px;
    height: 27px;
    cursor: pointer;
}



  
